import React, { Component } from "react"
import { Label, Input, FormGroup, Button } from "reactstrap"
import { X } from "react-feather"
import PerfectScrollbar from "react-perfect-scrollbar"
import {Spinner} from "reactstrap"
import classnames from "classnames"

class DataListSidebar extends Component {
  state = {
    //isLoading: props.isLoading,
    isLoading: this.props.isLoading,
    relation: 'Son',
    relation1: 'Son',
    email: '',
    address: '',
    contact1: '',
    contact2: '',
    id: "",
    name: "",
    category: "Audio",
    order_status: "pending",
    price: "",
    img: "",
    popularity: {
      popValue: ""
    }
  }

  addNew = false

  componentDidUpdate(prevProps, prevState) {
    console.log('aaaaaaaaaaaaaaaaa', this.props.isLoading)
    if (this.props.data !== null && prevProps.data === null) {
      if (this.props.data.id !== prevState.id) {
        this.setState({ id: this.props.data.id })
      }
      if (this.props.data.name !== prevState.name) {
        this.setState({ name: this.props.data.name })
      }
      if (this.props.data.relation !== prevState.relation) {
        this.setState({ relation: this.props.data.relation })
      }
      if (this.props.data.relation !== prevState.relation1) {
        this.setState({ relation1: this.props.data.relation })
      }
      if (this.props.data.address !== prevState.address) {
        this.setState({ address: this.props.data.address })
      }
      if (this.props.data.email !== prevState.email) {
        this.setState({ email: this.props.data.email })
      }
      console.log('hhhhhhhhhhhhhhhhhhhhh', this.props.data,prevState )
      if (this.props.data.primaryContact !== prevState.contact1) {
        this.setState({ contact1: this.props.data.primaryContact })
      }
      if (this.props.data.secondaryContact !== prevState.contact2) {
        this.setState({ contact2: this.props.data.secondaryContact })
      }
     
      if (this.props.data.order_status !== prevState.order_status) {
        this.setState({ order_status: this.props.data.order_status })
      }
      if (this.props.data.price !== prevState.price) {
        this.setState({ price: this.props.data.price })
      }
      if (this.props.data.img !== prevState.img) {
        this.setState({ img: this.props.data.img })
      }
    }
    if (this.props.data === null && prevProps.data !== null) {
      this.setState({
        id: "",
        name: "",
        
        category: "Son",
        order_status: "pending",
        price: "",
        img: "",
        popularity: {
          popValue: ""
        }
      })
    }
    if (this.addNew) {
      this.setState({
        id: "",
        name: "",
        category: "Son",
        order_status: "pending",
        price: "",
        img: "",
        popularity: {
          popValue: ""
        }
      })
    }
    this.addNew = false
  }

  handleSubmit = obj => {
    if (this.props.data !== null) {
      obj._id = this.props.data._id
      obj.usage = this.props.data.usage

      this.props.updateData(obj)
    } else {
      this.addNew = true
      this.props.addData(obj)
    }
    let params = Object.keys(this.props.dataParams).length
      ? this.props.dataParams
      : { page: 1, perPage: 4 }
    //this.props.handleSidebar(false, true)
    //this.props.getData(params)
  }

  render() {
    let { show, handleSidebar, data } = this.props
    
    let { name, relation,category, order_status, price, popularity, img } = this.state
    return (
      <div
        className={classnames("data-list-sidebar", {
          show: show
        })}>
        <div className="data-list-sidebar-header mt-2 px-2 d-flex justify-content-between">
          <h4>{data !== null ? "UPDATE NOMINEE DETAILS" : "ADD NEW NOMINEE"}</h4>
          <X size={20} onClick={() => handleSidebar(false, true)} />
        </div>
        {/* {this.props.isLoading && <Spinner color="primary" />}
        {!this.props.isLoading && */}
        <PerfectScrollbar
          className="data-list-fields px-2 mt-3"
          options={{ wheelPropagation: false }}>
          {this.props.thumbView && img.length ? (
            <FormGroup className="text-center">
              <img className="img-fluid" src={img} alt={name} />
              <div className="d-flex flex-wrap justify-content-between mt-2">
                <label
                  className="btn btn-flat-primary"
                  htmlFor="update-image"
                  color="primary">
                  Upload Image
                  <input
                    type="file"
                    id="update-image"
                    hidden
                    onChange={e =>
                      this.setState({
                        img: URL.createObjectURL(e.target.files[0])
                      })
                    }
                  />
                </label>
                <Button
                  color="flat-danger"
                  onClick={() => this.setState({ img: "" })}>
                  Remove Image
                </Button>
              </div>
            </FormGroup>
          ) : null}
          <FormGroup>
            <Label for="data-name">Name *</Label>
            <Input
              type="text"
              value={name}
              placeholder="Nominee Name"
              onChange={e => this.setState({ name: e.target.value })}
              id="data-name"
            />
          </FormGroup>
          <FormGroup>
            <Label for="data-category">Relation *</Label>
            <Input
              type="select"
              id="data-category"
              value={relation}
              onChange={e => this.setState({ relation: e.target.value, relation1: e.target.value })}>
              <option>Son</option>
              <option>Daughter</option>
              <option>Husband</option>
              <option>Wide</option>
              <option>Son-in-law</option>
              <option>Daughter-in-law</option>
              <option>Cousin</option>
              <option>Friend</option>
              <option>Colluege</option>
              <option>others</option>
            </Input>
          </FormGroup>
          {relation === "others" && <FormGroup>
            {/* <Label for="data-name">Relation </Label> */}
            <Input
              type="text"
              value={this.state.relation1}
              placeholder="Relation Name"
              onChange={e => this.setState({ relation1: e.target.value })}
              id="data-name"
            />
            </FormGroup>}
            <FormGroup>
            <Label for="data-name">Email *</Label>
            <Input
              type="text"
              value={this.state.email}
              placeholder="Enter nominee email"
              onChange={e => this.setState({ email: e.target.value })}
              id="data-name"
            />
            </FormGroup>
            <FormGroup>
            <Label for="data-price">Contact No *</Label>
            <Input
              type="number"
              value={this.state.contact1}
              onChange={e => this.setState({ contact1: e.target.value })}
              id="data-price"
              placeholder="Enter primary contact No"
            />
          </FormGroup>
          <FormGroup>
            <Label for="data-price">Secondary Contact No</Label>
            <Input
              type="number"
              value={this.state.contact2}
              onChange={e => this.setState({ contact2: e.target.value })}
              id="data-price"
              placeholder="Enter secondry contact No"
            />
          </FormGroup>
            <FormGroup>
            <Label for="data-name">Address *</Label>
            <Input
              type="textarea"
              value={this.state.address}
              placeholder="Adress"
              onChange={e => this.setState({ address: e.target.value })}
              id="data-name"
            />
            </FormGroup>
          {/* <FormGroup>
            <Label for="data-popularity">Popularity</Label>
            <Input
              type="number"
              value={popularity.popValue}
              id="data-popularity"
              placeholder="0 - 100%"
              onChange={e =>
                this.setState({
                  popularity: { popularity, popValue: e.target.value }
                })
              }
            />
          </FormGroup>
          <FormGroup>
            <Label for="data-status">Order Status</Label>
            <Input
              type="select"
              id="data-status"
              value={order_status}
              onChange={e => this.setState({ order_status: e.target.value })}>
              <option>pending</option>
              <option>canceled</option>
              <option>delivered</option>
              <option>on hold</option>
            </Input>
          </FormGroup> */}
          
          {this.props.thumbView && img.length <= 0 ? (
            <label
              className="btn btn-primary"
              htmlFor="upload-image"
              color="primary">
              Upload Image
              <input
                type="file"
                id="upload-image"
                hidden
                onChange={e =>
                  this.setState({ img: URL.createObjectURL(e.target.files[0]) })
                }
              />
            </label>
          ) : null}
        </PerfectScrollbar>
        <div className="data-list-sidebar-footer px-2 d-flex justify-content-start align-items-center mt-1">
          <Button color="primary" onClick={() => this.handleSubmit(this.state)}>
          {this.props.isLoading && <Spinner color="danger" size="sm"/>} {data !== null ? (this.props.isLoading ? "Updating..." : "Update") : (this.props.isLoading ? "Adding..." : "Add")} 
          </Button>
          <Button
            className="ml-1"
            color="danger"
            outline
            onClick={() => handleSidebar(false, true)}>
            Cancel
          </Button>
        </div>
      </div>
    )
  }
}
export default DataListSidebar
